// Import the WebAssembly memory at the top of the file.
import { memory } from "rusty-rogue-wasm/rusty_rogue_wasm_bg";
//import * as wasm from "hello-wasm-pack";
import { Map, TileType } from "rusty-rogue-wasm";

// Display deetz
const TILE_SIZE = 16; // px
const GRID_COLOR = "#CCCCCC";
const FLOOR_COLOR = "#FFFFFF";
const WALL_COLOR = "#000000";

// Init map
const MAP_WIDTH = 32;
const MAP_HEIGHT = 16;
const map = Map.new(MAP_WIDTH, MAP_HEIGHT);

// setup canvas
const canvas = document.getElementById("game-canvas");
canvas.height = (TILE_SIZE + 1) * MAP_HEIGHT + 1;
canvas.width = (TILE_SIZE + 1) * MAP_WIDTH + 1;
const ctx = canvas.getContext('2d');



const renderLoop = () => {

    // map.Tick();

    //drawGrid();
    drawTiles();


    requestAnimationFrame(renderLoop);

}

const drawGrid = () => {
    ctx.beginPath();
    ctx.strokeStyle = GRID_COLOR;

    // just draw around outside for now

    // top
    ctx.moveTo(0, 0);
    ctx.lineTo(MAP_WIDTH, 0);

    // bottom
    ctx.moveTo(0, MAP_HEIGHT);
    ctx.lineTo(MAP_WIDTH, MAP_HEIGHT);

    // left
    ctx.moveTo(0, 0);
    ctx.lineTo(0, MAP_HEIGHT);

    //right
    ctx.moveTo(MAP_WIDTH, 0);
    ctx.lineTo(MAP_WIDTH, MAP_HEIGHT);

    ctx.stroke();
};


const getIndex = (row, column) => {
    return row * MAP_WIDTH + column;
};


const drawTiles = () => {
    const tilesPtr = map.get_tiles();
    const tiles = new Uint8Array(memory.buffer, tilesPtr, MAP_WIDTH * MAP_HEIGHT);

    ctx.beginPath();

    for (let row = 0; row < MAP_HEIGHT; row++) {
        for (let col = 0; col < MAP_WIDTH; col++) {
            const idx = getIndex(row, col);

            ctx.fillStyle = tiles[idx] === TileType.Floor
                ? FLOOR_COLOR
                : WALL_COLOR;

            ctx.fillRect(
                col * (TILE_SIZE + 1) + 1,
                row * (TILE_SIZE + 1) + 1,
                TILE_SIZE,
                TILE_SIZE
            );
        }
    }

    ctx.stroke();
};



requestAnimationFrame(renderLoop);

